body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.weatherLocationCont{
    width: cal(100% - 20px);
    margin: 0 10px;
    min-width: 350px;
    box-sizing: border-box; /*Calculo del componente*/
    padding: 25px;
    font-family: 'Roboto', sans-serif;
    box-shadow: 10px 10px 40px -12px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.locationCont h2{
    margin: 0;
    white-space: nowrap;
}

.locationCont{
    width: 100%;
    height: 35px;
    text-align: left;
    padding-left: 10px;
    color: #888;
    line-height: 35px;
    overflow: hidden;
}
.weatherDataCont{
    background-color: rgba(200,200,200,0.5);
    width: 100%;
    height: 100px;
    line-height: 100px;
    border-radius: 5px;
    padding: 5px;
    box-sizing: border-box;
}
.weatherExtraInfoCont{
    width: 40%;
    display: inline-block;
    line-height: 28px;
    text-align: left;
}

.weatherTemperatureCont{
    width: 60%;
    display: inline-block;
}

.temperature {
    font-size: 28px;
    font-weight: bold;
    padding-left: 20px;
    color: #333
}

.temperatureType {
    font-weight: bold;
    color: #666;
}

.extraInfoText{
    color: #1535c5;
    font-size: 16px;
    font-weight: bold;
}

.wicon {
    color: rgb(255,72,0)
}

.locationList{
    max-height: 89vh;
    overflow-y: auto;
    width: 100%;
    margin-bottom: 20px;
}

@media (max-width: 765px) {
    .locationList{
        max-height: 35vh;
        overflow: auto;
        margin-bottom: 20px;
    }
}


.forecast-title{
    color: #888;
    text-align: center;
    padding: 15px 0;
}

.centerLoading{
    margin: 0 auto;
    width: 50px;
}


.details{
  background: ghostwhite;
  height: 88vh;
  min-height: 40vh;
  width: cal(100% - 40px);
  overflow: auto;
  padding: 0 20px;
}

@media (max-width: 765px) {
  .details{
    height: 55vh;
  }
}
